  background: linear-gradient(90deg, #dac5ed, #f2d5f5);
  border-radius: 8px;
  width: 0%;
  height: 100%;
  transition: width .3s ease-out;
  position: relative;
}

.loader-inner:after {
  content: "";
  background: linear-gradient(90deg, #0000, #fff6, #0000);
  width: 100%;
  height: 100%;
  animation: 1.5s infinite loading-shine;
  position: absolute;
  top: 0;
  left: 0;
}

