  display: flex;
}

.loader-blocks .block {
  opacity: .2;
  background: #dac5ed;
  border-radius: 2px;
  width: 12px;
  height: 12px;
  transition: all 1.3s;
  transform: scale(0);
}

.loader-blocks .block:first-child {
  animation: .5s .1s forwards block-appear;
}

.loader-blocks .block:nth-child(2) {
  animation: .5s .2s forwards block-appear;
}

.loader-blocks .block:nth-child(3) {
  animation: .5s .3s forwards block-appear;
}

.loader-blocks .block:nth-child(4) {
  animation: .5s .4s forwards block-appear;
}

.loader-blocks .block:nth-child(5) {
  animation: .5s .5s forwards block-appear;
}

.loader-blocks .block:nth-child(6) {
  animation: .5s .6s forwards block-appear;
}

.loader-blocks .block:nth-child(7) {
