}

.loader-blocks .block:nth-child(8) {
  animation: .5s .8s forwards block-appear;
}

.loader-blocks .block:nth-child(9) {
  animation: .5s .9s forwards block-appear;
}

.loader-blocks .block:nth-child(10) {
  animation: .5s 1s forwards block-appear;
}

.loader-blocks .block.active {
  opacity: 1;
  transform: scale(1);
}

